import Vue from 'vue'
import Router from 'vue-router'
// import SignPage from '../pages/SignPage.vue'
import BaseView from '../pages/BaseView.vue'
import TesisPage from '../pages/TesisPage.vue'
import ListTesisPage from '../pages/ListTesisPage.vue'
import SearchTesisPage from '../pages/SearchTesisPage.vue'
import StudentPage from '../pages/StudentPage.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    // {
    //   path: '/',
    //   name: 'SignPage',
    //   component: SignPage
    // },
    {
      path: '/',
      name: 'BaseView',
      component: BaseView,
      children: [
        {
          path: 'alumnos',
          name: 'StudentPage',
          component: StudentPage
        },
        {
          path: 'tesis',
          name: 'TesisPage',
          component: TesisPage
        },
        {
          path: 'listar-tesis',
          name: 'ListTesisPage',
          component: ListTesisPage
        },
        {
          path: 'busqueda',
          name: 'SearchTesisPage',
          component: SearchTesisPage
        }
      ]
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    // return { x: 0, y: 0 }
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



