<template>
  <div class="">
    <div class="row">
      <div class="col-xs-12 col-sm-6">
        <ui-textbox v-model="idalumno" name="codigo" label="Código"></ui-textbox>
      </div>
      <div class="col-xs-12 col-sm-6">
        <ui-datepicker placeholder="dd/mm/aaaa"
        picker-type="modal" :lang="dateEs" v-model="picker1">Seleccione fecha</ui-datepicker>
      </div>
    </div>
    <div >
      <ui-textbox v-model="titulo" multi-line name="titlo" label="Titulo"></ui-textbox>
    </div>
    <div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-12">
        <ui-select name="asesor" v-model="selectAsesor" :options="teachers" label="Asesor"></ui-select>
      </div>
    </div>
    <p>Número</p>
    <div class="row">
      <div class="col-xs-12 col-sm-2 col-md-1">
        <ui-textbox v-model="num1" name="num1"></ui-textbox>
      </div>
      <div class="col-xs-12 col-sm-2 col-md-1">
        <ui-textbox v-model="num2" name="num2" :disabled="true"></ui-textbox>
      </div>
      <div class="col-xs-12 col-sm-2 col-md-1">
        <ui-textbox v-model="num3" name="num3" :disabled="true"></ui-textbox>
      </div>
      <div class="col-xs-12 col-sm-2 col-md-1">
        <ui-textbox v-model="num4" name="num4"></ui-textbox>
      </div>
    </div>
    <div class="text-right">
      <ui-button @click="onSubmitInscripcionAsesor()" :loading="loadRegister" color="primary">Registrar</ui-button>
    </div>
    <ui-modal dismiss-on="close-button esc" ref="modalConfirmation">
      <div slot="header"><b :style="'color:' + modal.type === 'success' ? 'green':'red'">{{modal.title}}</b></div>
      {{modal.message}}
    </ui-modal>
  </div>
</template>
<script>
import { dateEs } from '../../mixins/text.js'
export default {
  name: 'DictamenInscripcion',
  props: {
    teachers: Array,
    student: Object
  },
  data () {
    return {
      dateEs,
      selectAsesor: '',
      picker1: null,
      titulo: '',
      loadSearch: false,
      loadRegister: false,
      idalumno: '',
      idDocumento: '',
      user: {},
      num1: '',
      num2: 'UPG',
      num3: 'FISI',
      num4: '',
      modal: {
        type: 'success',
        title: '',
        message: ''
      }
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    onSubmitInscripcionAsesor () {
      let data = {
        idalumno: this.student.cod_alumno,
        iddocente: parseInt(this.selectAsesor.value),
        titulo: this.titulo,
        nro_inscripcion: `${this.num1}-UPG-FISI-${this.num4}`,
        fecha_inscripcion: this.formatDate1(this.picker1)
      }
      this.loadRegister = true
      this.axios.post('/api/tesis/inscripcion', data)
      .then(res => {
        let resdata = res.data
        this.loadRegister = false
        console.log('registrar-tesis', resdata)
        this.modal.type = 'success'
        this.modal.title = 'Confirmación'
        this.modal.message = 'Se registro correctamente.'
        this.openModal('modalConfirmation')
        // console.log(res)
      }).catch(err => {
        this.loadRegister = false
        this.modal.type = 'error'
        this.modal.title = 'Error'
        this.modal.message = 'Hubo un error en el proceso vuelta a intentarlo.'
        this.openModal('modalConfirmation')
        console.log('error', err)
      })
    },
    formatDate1 (date) {
      let _date = new Date(date)
      var mm = _date.getMonth() + 1
      var dd = _date.getDate()
      return [
        _date.getFullYear(),
        (mm > 9 ? '' : '0') + mm,
        (dd > 9 ? '' : '0') + dd
      ].join('-')
    }
  }
}
</script>



