<template>
<div class="bgTransition">
  <template v-if="!responseContact">
    <div class="preload-app">
      <ui-progress-linear></ui-progress-linear>
    </div>
  </template>
  <template v-else>
    <section class="section-register">
      <div class="width-register clearfix">
        <h3 class="section--title">Busqueda de Alumno</h3>
        <div class="content-register clearfix">
          <div class="row">
            <div class="col-xs-12 col-sm-2">
              <ui-textbox v-model="searchCode" name="searchCode" label="Código de Alumno"></ui-textbox>
              <span>*Las horas son c</span>
            </div>
          </div>
        </div>
      </div>
    </section>
  </template>
</div>
</template>
<script>
import DictamenInscripcion from './components/DictamenInscripcion.vue'
import DictamenExpedito from './components/DictamenExpedito.vue'
import DesignacionJurado from './components/DesignacionJurado.vue'
import DesignacionExaminador from './components/DesignacionExaminador.vue'
import { dateEs } from '../mixins/text.js'
export default {
  name: 'TesisPage',
  components: {
    DictamenInscripcion,
    DictamenExpedito,
    DesignacionJurado,
    DesignacionExaminador
  },
  data () {
    return {
      searchCode: '',
      dateEs,
      selectAsesor: '',
      picker1: null,
      titulo: '',
      teachers: [],
      idDocumento: '',
      user: {},
      num1: '',
      num2: 'UPG',
      num3: 'FISI',
      num4: '',
      tesis: {
        idAlumno: '',
        numero: '',
        idAsesor: '',
        fecha: ''
      },
      codigoBuscar: '',
      responseContact: false
    }
  },
  created () {
    this.fetchTeacher()
  },
  mounted () {
  },
  methods: {
    fetchTeacher () {
      this.axios.get('/api/listarProfesor')
      .then(res => {
        let data = res.data.profesor
        console.log(data)
        data.forEach(item => {
          this.teachers.push({
            label: item.nombre,
            value: item.idProfesor
          })
        })
        this.responseContact = true
        // console.log(res)
      }).catch(err => {
        this.responseContact = true
        console.log('error', err)
      })
    },
    onSubmitSearchStudent () {
      if (this.isEmpty(this.codigoBuscar)) {
        return
      }
      this.axios.post('/api/buscarPorCodigo', { codigo: this.codigoBuscar })
      .then(res => {
        let data = res.data
        console.log(data)
        this.user = data.alumno
        this.loading = false
        // console.log(res)
      }).catch(err => {
        this.loading = false
        console.log('error', err)
      })
    },
    formatDate1 (date) {
      let _date = new Date(date)
      var mm = _date.getMonth() + 1
      var dd = _date.getDate()
      return [
        _date.getFullYear(),
        (mm > 9 ? '' : '0') + mm,
        (dd > 9 ? '' : '0') + dd
      ].join('-')
    },
    onSubmitRegisterTesis () {
      this.tesis.idAlumno = this.user.idAlumno
      this.tesis.idAsesor = parseInt(this.selectAsesor.value)
      this.tesis.titulo = this.titulo
      this.tesis.numero = `${this.num1}-UPG-FISI-${this.num4}`
      this.tesis.fecha = this.formatDate1(this.picker1)
      this.axios.post('/api/registrarTesis', this.tesis)
      .then(res => {
        let data = res.data
        console.log('registrar-tesis', data)
        this.loading = false
        // console.log(res)
      }).catch(err => {
        this.loading = false
        console.log('error', err)
      })
    }
  }
}
</script>



